body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.request {

    margin: 0 auto;
    width: 1000px;

}

.img-container {
    text-align: center;
    margin: 18px;
    padding: 80px;
    height: 70%;
    width: 80%;
    border: 1px solid grey;
    background-color: #3A4C9C;
    background-image: url(/reactImgurAPI/static/media/blocks.248753de.jpg);
 

}

.img-container li {
    display: inline-block;
   

}

a,h1,p {

    font-family: 'Roboto', sans-serif;
}

a {

    text-decoration: none;
    color: lightskyblue;
}

h1 {
   
    color: #3A4C9C;
    text-align: center;
    margin: 50px;
    
    

}

p {

    color: lightskyblue;
    text-align: center;
    margin: 20px;

}

.sectionOption {
    
    text-align: center;
    list-style: none;
    color: #3A4C9C;
   
}

.sectionOption li {
   
    color: lightskyblue;
    display: inline-block;
   

}

.sectionOption button {
    border: solid 1px #3A4C9C;
    background-color:#3A4C9C;
    margin: 15px;
    padding: 10px;
    padding-left: 20px;
    padding-right: 20px;
    cursor: pointer;
}

.sectionOption li button:hover {

    background-color: #668CFE;


}


@media (max-width:996px) {

    .img-container,h1,.sectionOption {
      
        max-width: 40%;
    
    }
    img {
        max-width: 100%;

    }
    .sectionOption {
        padding-left: 100px;

    }
    h1 {
        padding-left: 70px;


    }



}
.search {
    width: 100%;
    position: relative
  }
  
  .searchTerm {
    
    width: 100%;
    border: 3px solid #3A4C9C;;
    padding: 5px;
    height: 20px;
    border-radius: 5px;
    outline: none;
    color: #9DBFAF;
  }
  
  .searchTerm:focus{
    color:#3A4C9C;
  }
  
 
  /*Resize the wrap to see the search bar change!*/
  .wrap{
      
    width: 30%;
    position: absolute;
    top: 50%;
    left: 49%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
  }

  .wrapper {
    width: 100%;
    padding-top: 350px;

  }

  .searchContainer {
    background-color: #3A4C9C;
     border: 1px solid skyblue;
     text-align: center;
     width: 670px;
     height: 40%;
     margin: 20px auto;
     padding-top: 30px;
     padding-left: 50px;
     padding-right: 50px;
     padding-bottom: 20px;
     
    
  }

  .searchContainer li {
    list-style: none;
    display: inline;
  }


  .searchContainer:hover {
    
    background-color: #668CFE;
  }

  .searchContainer h3,a {

    color: lightskyblue;
  }


.nav {
    height: 58px;
    width: 100%;
    background-color: #3A4C9C;
    position: relative;
  }
  
  .nav > .nav-header {
    display: inline;
    
  }
  
  .nav > .nav-header > .nav-title {
    display: inline-block;
    height: 100%;
    text-align: center;
    font-size: 22px;
    color: lightskyblue;
    padding: 10px 20px 10px 20px;
  }
  
  .nav > .nav-btn {
    display: none;
    
  }
  
  .nav > .nav-links {
    display: inline;
    float: right;
    font-size: 18px;
  }
  
  .nav > .nav-links > a {
    display: inline-block;
    padding: 15px 20px 13px 20px;
    text-decoration: none;
    color: lightskyblue;
    height: 62px;
    font-family: 'Roboto', sans-serif;
  }
  
  .nav > .nav-links > a:hover {
    background-color: rgba(0, 0, 0, 0.3);
  }
  
  .nav > #nav-check {
    display: none;
  }
  
  @media (max-width:600px) {
    .nav > .nav-btn {
      display: inline-block;
      position: absolute;
      right: 0px;
      top: 0px;
    }
    .nav > .nav-btn > label {
      display: inline-block;
      width: 40px;
      height: 50px;
      padding: 13px;
    }
    .nav > .nav-btn > label:hover {
      background-color: rgba(0, 0, 0, 0.3);
    }
    .nav > .nav-btn > label > span {
      display: block;
      width: 25px;
      height: 10px;
      border-top: 2px solid lightskyblue;
    }
    .nav > .nav-links {
      position: absolute;
      display: block;
      width: 100%;
      background-color: #3A4C9C;
      height: 0px;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 50px;
      left: 0px;
    }
    .nav > .nav-links > a {
      display: block;
      width: 100%;
    }
    .nav > #nav-check:not(:checked) + .nav-links {
      height: 0px;
    }
    .nav > #nav-check:checked + .nav-links {
      height: calc(100vh - 50px);
      overflow-y: auto;
    }
  }
